/**
 * 
 * @param {Object} params - 
 * @param {string} params.permission - scope 'bluetooth'
 * @param {string} params.permissionName -  ''
 * @param {boolean} [params.customPopup=false] - 
 * @param {Function} [params.success] - 
 * @param {Function} [params.fail] - 
 * @param {Function} [params.cancel] - 
 */
function getPermission(params) {
    const permissionScope = `scope.${params.permission}`;
    const tipsContent = `${params.permissionName}`;

    function handleFailure() {
        if (params.customPopup && typeof params.customPopup === 'function') {
            params.customPopup(tipsContent, handleSetting);
        } else {
            uni.showModal({
                title: '',
                content: tipsContent,
                success: res => {
                    if (res.confirm) {
                        uni.openSetting();
                    } else {
                        params.cancel && params.cancel();
                    }
                }
            });
        }
    }

    function handleSetting() {
        uni.openSetting({
            success: (settingRes) => {
                if (settingRes.authSetting[permissionScope]) {
                    params.success && params.success();
                } else {
                    params.fail && params.fail();
                }
            }
        });
    }

    uni.getSetting({
        success(res) {
            if (res.authSetting.hasOwnProperty(permissionScope)) {
                if (!res.authSetting[permissionScope]) {
                    handleFailure();
                } else {
                    params.success && params.success();
                }
            } else {
                uni.authorize({
                    scope: permissionScope,
                    success: params.success,
                    fail: handleFailure
                });
            }
        },
        fail: params.fail
    });
}

module.exports = {
    getPermission,
};
